:root {
  --primary-color: #dfdfe2;
  --secondary-color: #fff;
  --app-background-color: #4d4d62;
  --background-color: #1b1b32;
  --foreground-color: #3b3b4f;
  --highlight-color: #f1be32;
  --root-font-size: 16px;
  font-size: var(--root-font-size);
  --font-headline: "Roboto Mono", monospace;
  --font-family: "Lato", sans-serif;
}

*, :after, :before {
  box-sizing: border-box;
}

body {
  background-color: var(--app-background-color);
  color: var(--primary-color);
  font-family: var(--font-family);
}

h1 {
  font-size: 1.125rem;
  line-height: 1.6;
}

h2 {
  font-size: var(--root-font-size);
}

ul {
  margin: 0;
}

.container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 5px;
  margin-top: 10px;
  display: flex;
}

.player, .playlist {
  background-color: var(--background-color);
  border: 3px solid var(--foreground-color);
  width: 450px;
}

.player {
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  height: 260px;
  padding: 10px;
  display: flex;
}

.player-bar, .playlist-bar {
  background-color: var(--foreground-color);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 30px;
  padding: 0 5px;
  display: flex;
}

.parallel-lines {
  flex-wrap: wrap;
  row-gap: 6px;
  padding: 0 5px;
  display: flex;
}

.parallel-lines > div {
  background-color: var(--highlight-color);
  width: 100%;
  min-width: 75px;
  height: 2px;
}

.fcc-title, .playlist-title {
  color: var(--secondary-color);
  font-family: var(--font-headline);
  margin: 0 10px;
}

.player-content {
  background-color: var(--foreground-color);
  justify-content: center;
  align-items: center;
  column-gap: 13px;
  width: 430px;
  height: 200px;
  display: flex;
}

#player-album-art {
  background-color: var(--secondary-color);
  border: 6px solid var(--background-color);
}

#player-album-art img {
  width: 150px;
  display: block;
}

.player-display {
  background-color: var(--background-color);
  flex-direction: column;
  row-gap: 20px;
  width: 226px;
  height: 153px;
  padding: 14px;
  display: flex;
}

.player-display-song-artist {
  height: 80px;
}

.player-buttons svg {
  fill: var(--primary-color);
}

.playing > svg {
  fill: var(--highlight-color);
}

.player-buttons {
  justify-content: space-around;
  display: flex;
}

button {
  color: var(--primary-color);
  cursor: pointer;
  font-size: var(--root-font-size);
  outline-color: var(--highlight-color);
  text-align: center;
  background: none;
  border: none;
}

.playlist-song {
  outline-color: var(--highlight-color);
}

.playlist li:not(:last-child) {
  border-bottom: 1px solid var(--background-color);
}

button:focus, .playlist-song:focus {
  outline-width: 2px;
  outline-style: dashed;
}

.playlist {
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  height: auto;
  padding: 10px;
  display: flex;
}

#playlist-songs {
  background-color: var(--foreground-color);
  visibility: visible;
  flex-direction: column;
  justify-content: start;
  row-gap: 8px;
  width: 430px;
  height: 100%;
  padding: 8px 9px;
  list-style: none;
  display: flex;
}

.playlist-song {
  justify-content: space-between;
  align-items: center;
  height: 55px;
  padding: 5px;
  display: flex;
}

[aria-current="true"] {
  background-color: var(--background-color);
}

[aria-current="true"] p {
  color: var(--highlight-color);
}

.playlist-song-info {
  font-family: var(--font-family);
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  column-gap: 7px;
  height: 100%;
  padding: 5px 0;
  display: flex;
}

#player-song-title, #player-song-artist {
  margin: 0;
}

#player-song-artist {
  color: var(--highlight-color);
  font-size: .75rem;
}

#player-song-title {
  font-size: 1.125rem;
}

.playlist-song-title {
  text-align: left;
  width: 241px;
  font-size: .85rem;
}

.playlist-song-artist {
  width: 80px;
  font-size: .725rem;
}

.playlist-song-duration {
  font-size: .725rem;
  font-family: var(--font-headline);
  width: 30px;
  margin: auto;
}

.playlist-song-delete {
  width: 20px;
  height: 20px;
  padding: 0;
}

.playlist-song-delete, .playlist-song-delete {
  fill: var(--foreground-color);
}

.playlist-song-delete:hover circle, .playlist-song-delete:focus circle {
  fill: red;
}

@media (width <= 700px) {
  .player, .playlist {
    width: 300px;
  }

  .player {
    height: 340px;
  }

  #playlist-songs {
    scrollbar-color: var(--background-color) var(--secondary-color);
    scrollbar-width: thin;
    height: 280px;
    padding: 5px 6px;
    overflow: hidden scroll;
  }

  #playlist-songs::-webkit-scrollbar {
    width: 5px;
  }

  #playlist-songs::-webkit-scrollbar-track {
    background: var(--background-color);
  }

  #playlist-songs::-webkit-scrollbar-thumb {
    background: var(--secondary-color);
  }

  h1 {
    font-size: .813rem;
  }

  h2 {
    font-size: .75rem;
  }

  .player-bar, .playlist-bar, .player-content, #playlist-songs {
    width: 280px;
  }

  .playlist-song {
    justify-content: space-between;
  }

  .playlist-song-title {
    width: 140px;
  }

  .playlist-song-artist {
    width: 40px;
  }

  .playlist-song-duration > button {
    padding: 0;
  }

  .player-content {
    justify-items: center;
    height: 100%;
    display: inline;
    position: relative;
  }

  #player-album-art {
    z-index: -100;
    box-shadow: none;
    background: #000;
    height: 280px;
  }

  #player-album-art img {
    opacity: .6;
    width: 100%;
  }

  .player-display-song-artist {
    padding: 0 10px;
  }

  .player-display-song-artist > p {
    white-space: pre-wrap;
  }

  .player-display {
    z-index: 1000;
    text-align: center;
    background-color: #0000;
    justify-content: space-between;
    width: 100%;
    height: 280px;
    position: absolute;
    top: 0;
  }
}
